.members-container {
    box-shadow: 0px 0px 0px 2px #aeaeaeb5;
    border-radius: 4px;
    background: white;
    position: absolute;
    width: 270px;
    top: 185px;
    right: 21px;
    padding-top: 25px;

    p {
        margin-inline-start: 0px;
        padding: 7px 0px
    }

    hr {
        margin: 0 5px;
    }

    &-close-btn {
        cursor: pointer;
        position: absolute;
        top: 5px;
        right: 5px;
        width: 15px;
        height: 15px;
    }

    .team-member {
        cursor: pointer;
        margin-bottom: 5px;
        border-radius: 2px;
        padding: 10px;
        border-bottom: 1px solid rgba(92, 92, 92, 0.288);

        &-icon {
            &-wrapper {
                width: 40px;
                height: 40px;
                margin-right: 5px;
                border: 1px solid;
                padding: 5px 7px;
                border-radius: 50%;
            }

            p {
                font-family: montserratExtraBold, 'sans-sarif';
                color: white;
            }
        }

        transition: 0.3s;

        &:hover {
            background: rgba(124, 124, 124, 0.438);
        }
    }

    &-add-team-members {
        padding: 10px;
    }

}